body {
  font-family: sans-serif;
}

input {
  border: 2px solid #000;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #19ddb6;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.article-card {
  width: 30%;
  text-align: center;
  background-color: #c1c1c1;
  border-radius: 15px;
  padding: 20px;
}

.article-img {
  width: 100%;
  border-radius: 15px;
}

.article-link {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(121, 154, 138, .73);
  border-radius: 15px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

